import React, { useEffect, useState } from 'react';
import NavBar from '../../components/navBar/index'
import moment from 'moment'
import './css/index.css'

interface PropsType {
    history: any;
}

const Home = (props: PropsType) => {
    useEffect(() => {
        setTime(moment().format('HH:mm'))
        const timer = setInterval(() => {
            setTime(moment().format('HH:mm'))
        }, 60000)
        return () => {
            clearInterval(timer)
        }
    }, [])

    const [time, setTime] = useState<string>('');

    return (
        <div>
            <NavBar></NavBar>
            <div className="home">
                <div className="home-left">
                    <p onClick={() => {props.history.push({pathname:"/management" + '/dataDaily'})}}><span><i className="iconfont icon-gongzuotai"></i><span>工作台</span></span></p>
                    <p onClick={() => {props.history.push({pathname:"/management" + '/accountStatistics'})}}><span><i className="iconfont icon-DRGshoufufeibiaozhunchaxun"></i><span>客户管理</span></span></p>
                    <p onClick={() => {props.history.push({pathname:"/management" + '/meetingUser'})}}><span><i className="iconfont icon-renyuanguanli1"></i><span>用户、服务</span></span></p>
                    <p onClick={() => {props.history.push({pathname:"/management" + '/dataDaily'})}}><span><i className="iconfont icon-shezhi"></i><span>系统管理</span></span></p>
                </div>
                <div className="home-right">
                    <div>
                        <p>{ time }</p>
                        <p>{moment().format('YYYY年MM月DD日')}</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Home
